.wpte-product-container {

    .wpte-product-caption-hover-style-6 {
        &,
        &:before,
        &:after,
        & .wpte-product-hover-figure,
        & .wpte-product-hover-figure:before,
        & .wpte-product-hover-figure:after,
        & .wpte-product-hover-figure-caption,
        & .wpte-product-hover-figure-caption:before,
        & .wpte-product-hover-figure-caption:after {
            background-color: rgba(126, 0, 184, 1);
        }
    }

    [class^='wpte-product-circle-'],
    [class*='wpte-product-circle-'] {
        overflow: hidden;
    }

    [class^='wpte-product-circle-']:before,
    [class*='wpte-product-circle-']:before {
        position: absolute;
        top: -25%;
        bottom: -25%;
        left: -25%;
        right: -25%;
        content: '';
        border-radius: 50%;
        transition: all 0.35s linear;
    }

    [class^='wpte-product-circle-'] .wpte-product-hover-figure-caption,
    [class*=' wpte-product-circle-'] .wpte-product-hover-figure-caption {
        opacity: 0;
        background-color: transparent !important;
    }

    [class^='wpte-product-circle-']:hover:before,
    [class*=' wpte-product-circle-']:hover:before {
        transform: translate(0, 0);
    }

    [class^='wpte-product-circle-']:hover .wpte-product-hover-figure-caption,
    [class*=' wpte-product-circle-']:hover .wpte-product-hover-figure-caption {
        opacity: 1;
        transition-delay: 0.35s;
    }

    .wpte-product-circle-up:before {
        transform: translateY(100%);
    }

    .wpte-product-circle-down:before {
        transform: translateY(-100%);
    }

    .wpte-product-circle-left:before {
        transform: translateX(100%);
    }

    .wpte-product-circle-right:before {
        transform: translateX(-100%);
    }

    .wpte-product-circle-top-left:before {
        transform: translate(-100%, -100%);
    }

    .wpte-product-circle-top-right:before {
        transform: translate(100%, -100%);
    }

    .wpte-product-circle-bottom-left:before {
        transform: translate(-100%, 100%);
    }

    .wpte-product-circle-bottom-right:before {
        transform: translate(100%, 100%);
    }
}